import React, { useState, useEffect, useRef } from 'react';
import { MOCK_USERS, MOCK_DATABASES } from '../constants';
import { SystemUser, SystemDatabase, UserRole } from '../types';
import { getDeviceId, validateLicenseKey } from '../utils/security';
import { 
  Users, Database, Lock, Plus, Trash2, CheckCircle, XCircle, 
  Search, Key, Shield, Save, Server, CreditCard, HardDrive 
} from 'lucide-react';
import toast, { Toaster } from 'react-hot-toast';
import Modal from 'react-modal';

interface SettingsProps {
  onSubscriptionChange?: () => void;
}

Modal.setAppElement('#root');

const Settings: React.FC<SettingsProps> = ({ onSubscriptionChange }) => {
  const [activeTab, setActiveTab] = useState<'users' | 'databases' | 'security' | 'subscription' | 'backup'>('users');
  const [users, setUsers] = useState<SystemUser[]>(MOCK_USERS);
  const [databases, setDatabases] = useState<SystemDatabase[]>(MOCK_DATABASES);

  const [userSearch, setUserSearch] = useState('');
  const [dbSearch, setDbSearch] = useState('');

  const [licenseKey, setLicenseKey] = useState('');
  const [isPaid, setIsPaid] = useState(false);
  const [deviceId, setDeviceId] = useState('');
  const [expiryDate, setExpiryDate] = useState<string | null>(null);

  const [backupName, setBackupName] = useState(`Nizam_Backup_${new Date().toISOString().split('T')[0]}`);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const [isUserModalOpen, setIsUserModalOpen] = useState(false);
  const [isDBModalOpen, setIsDBModalOpen] = useState(false);
  const [isPasswordModalOpen, setIsPasswordModalOpen] = useState(false); 
  const [selectedUser, setSelectedUser] = useState<SystemUser | null>(null);

  const [userForm, setUserForm] = useState({ username: '', fullName: '', email: '', role: UserRole.EMPLOYEE, password: '' });
  const [dbForm, setDbForm] = useState({ name: '', companyName: '' });
  const [adminPasswordForm, setAdminPasswordForm] = useState({ current: '', new: '', confirm: '' });
  const [userPasswordForm, setUserPasswordForm] = useState('');

  useEffect(() => {
    const status = localStorage.getItem('subscriptionStatus');
    const savedExpiry = localStorage.getItem('subscriptionExpiry');
    if (status === 'paid') {
      setIsPaid(true);
      if (savedExpiry) setExpiryDate(savedExpiry);
    }
    setDeviceId(getDeviceId());
  }, []);

  // Subscription Handlers
  const handleActivateLicense = (e: React.FormEvent) => {
    e.preventDefault();
    const validation = validateLicenseKey(licenseKey, deviceId);
    if (validation.isValid) {
      localStorage.setItem('subscriptionStatus', 'paid');
      if (validation.expiryDate) {
        localStorage.setItem('subscriptionExpiry', validation.expiryDate);
        setExpiryDate(validation.expiryDate);
      }
      setIsPaid(true);
      toast.success(`  !   : ${validation.expiryDate}`);
      if (onSubscriptionChange) onSubscriptionChange();
    } else {
      toast.error(validation.message);
    }
  };

  const handleResetTrial = () => {
    if (window.confirm('    ')) {
      localStorage.removeItem('subscriptionStatus');
      localStorage.removeItem('subscriptionExpiry');
      setIsPaid(false);
      setLicenseKey('');
      setExpiryDate(null);
      toast.success('   ');
      if (onSubscriptionChange) onSubscriptionChange();
    }
  };

  const copyDeviceId = () => {
    navigator.clipboard.writeText(deviceId);
    toast.success('   ');
  };

  // Backup Handlers
  const handleCreateBackup = () => {
    const backupData = {
      meta: {
        version: '1.0.0',
        date: new Date().toISOString(),
        type: 'full_backup',
        system: 'Nizam HR'
      },
      data: { localStorage: { ...localStorage }, runtime: { users, databases } }
    };
    const blob = new Blob([JSON.stringify(backupData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `${backupName}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
    toast.success('   ');
  };

  const handleRestoreBackup = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        const content = event.target?.result as string;
        const parsed = JSON.parse(content);
        if (parsed.meta?.system === 'Nizam HR') {
          if (window.confirm(`    ${parsed.meta.date}.   `)) {
            localStorage.clear();
            Object.entries(parsed.data.localStorage).forEach(([k, v]) => localStorage.setItem(k, v as string));
            toast.success('   .   .');
            window.location.reload();
          }
        } else toast.error('    .');
      } catch (err) { console.error(err); toast.error('   '); }
    };
    reader.readAsText(file);
    if (fileInputRef.current) fileInputRef.current.value = '';
  };

  // User Handlers
  const handleAddUser = (e: React.FormEvent) => {
    e.preventDefault();
    const newUser: SystemUser = { id: `U${Date.now()}`, ...userForm, active: true, lastLogin: '-' };
    setUsers([...users, newUser]);
    setIsUserModalOpen(false);
    setUserForm({ username: '', fullName: '', email: '', role: UserRole.EMPLOYEE, password: '' });
    toast.success('  ');
  };

  const toggleUserStatus = (id: string) => {
    setUsers(users.map(u => u.id === id ? { ...u, active: !u.active } : u));
    toast.success('   ');
  };

  const handleChangeUserPassword = (e: React.FormEvent) => {
    e.preventDefault();
    toast.success(`     ${selectedUser?.username}`);
    setIsPasswordModalOpen(false);
    setUserPasswordForm('');
    setSelectedUser(null);
  };

  // Database Handlers
  const handleCreateDatabase = (e: React.FormEvent) => {
    e.preventDefault();
    const newDB: SystemDatabase = { id: `DB${Date.now()}`, ...dbForm, status: 'active', createdAt: new Date().toISOString().split('T')[0], usersCount: 0 };
    setDatabases([...databases, newDB]);
    setIsDBModalOpen(false);
    setDbForm({ name: '', companyName: '' });
    toast.success('   ');
  };

  // Admin Handlers
  const handleChangeAdminPassword = (e: React.FormEvent) => {
    e.preventDefault();
    if (adminPasswordForm.new !== adminPasswordForm.confirm) {
      toast.error('   ');
      return;
    }
    toast.success('    ');
    setAdminPasswordForm({ current: '', new: '', confirm: '' });
  };

  const filteredUsers = users.filter(u => u.username.toLowerCase().includes(userSearch.toLowerCase()) || u.fullName.toLowerCase().includes(userSearch.toLowerCase()));
  const filteredDBs = databases.filter(db => db.name.toLowerCase().includes(dbSearch.toLowerCase()) || db.companyName.toLowerCase().includes(dbSearch.toLowerCase()));

  return (
    <div className="space-y-6 animate-in fade-in duration-500">
      <Toaster position="top-right" />
      <div>
        <h2 className="text-2xl font-bold text-gray-800"> </h2>
        <p className="text-sm text-gray-500">     </p>
      </div>
      {/* Tabs */}
      <div className="flex border-b border-gray-200 bg-white px-4 rounded-t-xl overflow-x-auto">
        {['users','databases','security','backup','subscription'].map(tab => (
          <button key={tab} onClick={() => setActiveTab(tab as any)}
            className={`flex items-center gap-2 px-6 py-4 border-b-2 transition-colors font-medium whitespace-nowrap ${activeTab===tab?'border-indigo-600 text-indigo-600':'border-transparent text-gray-500 hover:text-gray-700'}`}>
            {tab==='users'&&<Users className="h-5 w-5" />} {tab==='databases'&&<Database className="h-5 w-5" />} {tab==='security'&&<Lock className="h-5 w-5" />} {tab==='backup'&&<HardDrive className="h-5 w-5" />} {tab==='subscription'&&<CreditCard className="h-5 w-5" />}
            {tab==='users'?'':tab==='databases'?' ':tab==='security'?'':tab==='backup'?' ':' '}
          </button>
        ))}
      </div>

      <div className="bg-white p-6 rounded-b-xl shadow-sm border border-gray-100 border-t-0 min-h-[500px]">
        {/*          */}
      </div>
    </div>
  );
};

export default Settings;
