
import React, { useState, useEffect } from 'react';
import { MOCK_VEHICLES, MOCK_DRIVERS, MOCK_TRIPS, MOCK_MAINTENANCE } from '../constants';
import { Vehicle, Driver, Trip, MaintenanceLog } from '../types';
import { Car, Users, Map, Wrench, Plus, Search, Navigation, MapPin, Activity } from 'lucide-react';
import DataControls from '../components/DataControls';

const Transport: React.FC = () => {
  const [activeTab, setActiveTab] = useState<'vehicles' | 'drivers' | 'trips' | 'maintenance' | 'gps'>('vehicles');
  
  // State
  const [vehicles, setVehicles] = useState<Vehicle[]>(MOCK_VEHICLES || []);
  const [drivers, setDrivers] = useState<Driver[]>(MOCK_DRIVERS || []);
  const [trips, setTrips] = useState<Trip[]>(MOCK_TRIPS || []);
  const [maintenanceLogs, setMaintenanceLogs] = useState<MaintenanceLog[]>(MOCK_MAINTENANCE || []);
  const [searchTerm, setSearchTerm] = useState('');

  // Modals State (Simulated)
  const [isAddTripOpen, setIsAddTripOpen] = useState(false);
  const [newTrip, setNewTrip] = useState({
     vehicleId: '', driverId: '', startOdometer: 0, endOdometer: 0, route: ''
  });

  const isAdmin = true;

  // Helpers
  const handleAddTrip = (e: React.FormEvent) => {
     e.preventDefault();
     const vehicle = vehicles.find(v => v.id === newTrip.vehicleId);
     const driver = drivers.find(d => d.id === newTrip.driverId);
     
     if(vehicle && driver) {
        const trip: Trip = {
           id: `TR-${Date.now()}`,
           date: new Date().toISOString().split('T')[0],
           vehicleId: vehicle.id,
           plateNumber: vehicle.plateNumber,
           driverId: driver.id,
           driverName: driver.employeeName,
           startOdometer: Number(newTrip.startOdometer),
           endOdometer: Number(newTrip.endOdometer),
           distance: Number(newTrip.endOdometer) - Number(newTrip.startOdometer),
           route: newTrip.route,
           status: 'completed'
        };
        setTrips([trip, ...trips]);
        setIsAddTripOpen(false);
        // Update vehicle odometer
        setVehicles(vehicles.map(v => v.id === vehicle.id ? {...v, currentOdometer: Number(newTrip.endOdometer)} : v));
     }
  };

  const handleImportVehicles = (data: any[]) => {
      setVehicles([...vehicles, ...data.map((d, i) => ({...d, id: `IMP-V-${i}`}))]);
  };

  return (
    <div className="space-y-6 animate-in fade-in duration-500">
      <div>
        <h2 className="text-2xl font-bold text-gray-800">  </h2>
        <p className="text-sm text-gray-500">     </p>
      </div>

      {/* Tabs */}
      <div className="flex border-b border-gray-200 bg-white px-4 rounded-t-xl overflow-x-auto">
        <button onClick={() => setActiveTab('vehicles')} className={`flex items-center gap-2 px-6 py-4 border-b-2 transition-colors font-medium whitespace-nowrap ${activeTab === 'vehicles' ? 'border-indigo-600 text-indigo-600' : 'border-transparent text-gray-500 hover:text-gray-700'}`}><Car className="h-5 w-5" /></button>
        <button onClick={() => setActiveTab('drivers')} className={`flex items-center gap-2 px-6 py-4 border-b-2 transition-colors font-medium whitespace-nowrap ${activeTab === 'drivers' ? 'border-indigo-600 text-indigo-600' : 'border-transparent text-gray-500 hover:text-gray-700'}`}><Users className="h-5 w-5" /></button>
        <button onClick={() => setActiveTab('trips')} className={`flex items-center gap-2 px-6 py-4 border-b-2 transition-colors font-medium whitespace-nowrap ${activeTab === 'trips' ? 'border-indigo-600 text-indigo-600' : 'border-transparent text-gray-500 hover:text-gray-700'}`}><Map className="h-5 w-5" /></button>
        <button onClick={() => setActiveTab('maintenance')} className={`flex items-center gap-2 px-6 py-4 border-b-2 transition-colors font-medium whitespace-nowrap ${activeTab === 'maintenance' ? 'border-indigo-600 text-indigo-600' : 'border-transparent text-gray-500 hover:text-gray-700'}`}><Wrench className="h-5 w-5" /></button>
        <button onClick={() => setActiveTab('gps')} className={`flex items-center gap-2 px-6 py-4 border-b-2 transition-colors font-medium whitespace-nowrap ${activeTab === 'gps' ? 'border-red-600 text-red-600' : 'border-transparent text-gray-500 hover:text-gray-700'}`}><Navigation className="h-5 w-5" /> (GPS)</button>
      </div>

      <div className="bg-white p-6 rounded-b-xl shadow-sm border border-gray-100 border-t-0 min-h-[500px]">
         
         {/* --- VEHICLES TAB --- */}
         {activeTab === 'vehicles' && (
            <div className="space-y-4">
               <div className="flex justify-between items-center">
                  <div className="relative w-72">
                     <Search className="absolute right-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                     <input type="text" placeholder="  ..." className="w-full rounded-lg border border-gray-200 py-2 pr-9 pl-4 focus:border-indigo-500 focus:outline-none" value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)} />
                  </div>
                  <div className="flex gap-2">
                     <DataControls data={vehicles} fileName="vehicles_list" isAdmin={isAdmin} onImport={handleImportVehicles} />
                     <button className="flex items-center gap-2 bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 transition">
                        <Plus className="h-5 w-5" />  
                     </button>
                  </div>
               </div>
               <div className="overflow-x-auto border border-gray-100 rounded-lg">
                  <table className="w-full text-right text-sm">
                     <thead className="bg-gray-50 text-gray-500">
                        <tr>
                           <th className="px-6 py-4"></th>
                           <th className="px-6 py-4"> </th>
                           <th className="px-6 py-4"></th>
                           <th className="px-6 py-4"> </th>
                           <th className="px-6 py-4"> ()</th>
                           <th className="px-6 py-4"></th>
                        </tr>
                     </thead>
                     <tbody className="divide-y divide-gray-100">
                        {vehicles.filter(v => v.plateNumber.includes(searchTerm)).map(v => (
                           <tr key={v.id} className="hover:bg-gray-50">
                              <td className="px-6 py-4 font-medium">{v.model} <span className="text-xs text-gray-500 bg-gray-100 px-2 py-0.5 rounded mr-2">{v.type}</span></td>
                              <td className="px-6 py-4 font-mono">{v.plateNumber}</td>
                              <td className="px-6 py-4">
                                 <span className={`px-2 py-1 rounded-full text-xs ${v.status === 'active' ? 'bg-green-100 text-green-800' : v.status === 'maintenance' ? 'bg-yellow-100 text-yellow-800' : 'bg-red-100 text-red-800'}`}>
                                    {v.status === 'active' ? '' : v.status === 'maintenance' ? ' ' : ''}
                                 </span>
                              </td>
                              <td className="px-6 py-4 font-mono">{v.licenseExpiry}</td>
                              <td className="px-6 py-4 font-mono">{v.currentOdometer.toLocaleString()}</td>
                              <td className="px-6 py-4"><button className="text-indigo-600 hover:underline"> </button></td>
                           </tr>
                        ))}
                     </tbody>
                  </table>
               </div>
            </div>
         )}

         {/* --- DRIVERS TAB --- */}
         {activeTab === 'drivers' && (
            <div className="space-y-4">
               <div className="flex justify-between items-center">
                  <div className="relative w-72">
                     <Search className="absolute right-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                     <input type="text" placeholder="  ..." className="w-full rounded-lg border border-gray-200 py-2 pr-9 pl-4 focus:border-indigo-500 focus:outline-none" />
                  </div>
                  <button className="flex items-center gap-2 bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 transition">
                     <Plus className="h-5 w-5" />   
                  </button>
               </div>
               <div className="overflow-x-auto border border-gray-100 rounded-lg">
                  <table className="w-full text-right text-sm">
                     <thead className="bg-gray-50 text-gray-500">
                        <tr>
                           <th className="px-6 py-4"></th>
                           <th className="px-6 py-4"> </th>
                           <th className="px-6 py-4"> </th>
                           <th className="px-6 py-4"> </th>
                           <th className="px-6 py-4"></th>
                        </tr>
                     </thead>
                     <tbody className="divide-y divide-gray-100">
                        {drivers.map(d => (
                           <tr key={d.id} className="hover:bg-gray-50">
                              <td className="px-6 py-4 font-medium">{d.employeeName}</td>
                              <td className="px-6 py-4 font-mono">{d.licenseNumber}</td>
                              <td className="px-6 py-4">{d.licenseType}</td>
                              <td className="px-6 py-4 font-mono">{d.licenseExpiry}</td>
                              <td className="px-6 py-4">
                                 <span className={`px-2 py-1 rounded-full text-xs ${d.status === 'active' ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}`}>
                                    {d.status === 'active' ? '' : ''}
                                 </span>
                              </td>
                           </tr>
                        ))}
                     </tbody>
                  </table>
               </div>
            </div>
         )}

         {/* --- TRIPS TAB --- */}
         {activeTab === 'trips' && (
            <div className="space-y-4">
               <div className="flex justify-between items-center">
                  <DataControls data={trips} fileName="trips_report" isAdmin={isAdmin} />
                  <button onClick={() => setIsAddTripOpen(true)} className="flex items-center gap-2 bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 transition">
                     <Plus className="h-5 w-5" />  
                  </button>
               </div>
               
               {/* New Trip Modal (Simulated inline) */}
               {isAddTripOpen && (
                  <div className="bg-gray-50 p-4 rounded-xl border border-indigo-100 mb-4 animate-in fade-in slide-in-from-top-2">
                     <h4 className="font-bold text-indigo-700 mb-3">  </h4>
                     <form onSubmit={handleAddTrip} className="grid grid-cols-1 md:grid-cols-5 gap-4 items-end">
                        <div>
                           <label className="block text-xs text-gray-500 mb-1"></label>
                           <select className="w-full p-2 rounded border" required onChange={e => setNewTrip({...newTrip, vehicleId: e.target.value})}>
                              <option value=""> </option>
                              {vehicles.filter(v => v.status === 'active').map(v => <option key={v.id} value={v.id}>{v.plateNumber} - {v.model}</option>)}
                           </select>
                        </div>
                        <div>
                           <label className="block text-xs text-gray-500 mb-1"></label>
                           <select className="w-full p-2 rounded border" required onChange={e => setNewTrip({...newTrip, driverId: e.target.value})}>
                              <option value=""> </option>
                              {drivers.filter(d => d.status === 'active').map(d => <option key={d.id} value={d.id}>{d.employeeName}</option>)}
                           </select>
                        </div>
                        <div>
                           <label className="block text-xs text-gray-500 mb-1"> </label>
                           <input type="number" className="w-full p-2 rounded border" required placeholder="" onChange={e => setNewTrip({...newTrip, startOdometer: Number(e.target.value)})} />
                        </div>
                        <div>
                           <label className="block text-xs text-gray-500 mb-1"> </label>
                           <input type="number" className="w-full p-2 rounded border" required placeholder="" onChange={e => setNewTrip({...newTrip, endOdometer: Number(e.target.value)})} />
                        </div>
                        <div>
                           <button type="submit" className="w-full bg-green-600 text-white p-2 rounded hover:bg-green-700"> </button>
                        </div>
                     </form>
                     <div className="mt-2">
                        <label className="block text-xs text-gray-500 mb-1"> </label>
                        <input type="text" className="w-full p-2 rounded border" placeholder=" ...  ..." onChange={e => setNewTrip({...newTrip, route: e.target.value})} />
                     </div>
                  </div>
               )}

               <div className="overflow-x-auto border border-gray-100 rounded-lg">
                  <table className="w-full text-right text-sm">
                     <thead className="bg-gray-50 text-gray-500">
                        <tr>
                           <th className="px-6 py-4"></th>
                           <th className="px-6 py-4"></th>
                           <th className="px-6 py-4"></th>
                           <th className="px-6 py-4"> ()</th>
                           <th className="px-6 py-4"> </th>
                           <th className="px-6 py-4"></th>
                        </tr>
                     </thead>
                     <tbody className="divide-y divide-gray-100">
                        {trips.map(t => (
                           <tr key={t.id} className="hover:bg-gray-50">
                              <td className="px-6 py-4 font-mono">{t.date}</td>
                              <td className="px-6 py-4 font-mono">{t.plateNumber}</td>
                              <td className="px-6 py-4">{t.driverName}</td>
                              <td className="px-6 py-4 font-bold">{t.distance} </td>
                              <td className="px-6 py-4">{t.route}</td>
                              <td className="px-6 py-4"><span className="text-green-600 text-xs font-bold bg-green-50 px-2 py-1 rounded"></span></td>
                           </tr>
                        ))}
                     </tbody>
                  </table>
               </div>
            </div>
         )}

         {/* --- MAINTENANCE TAB --- */}
         {activeTab === 'maintenance' && (
            <div className="space-y-6">
               <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <div className="bg-orange-50 p-4 rounded-xl border border-orange-100">
                     <p className="text-orange-600 text-sm">  </p>
                     <p className="text-2xl font-bold text-gray-800 mt-1">{maintenanceLogs.reduce((a,c) => a + c.cost, 0).toLocaleString()} .</p>
                  </div>
               </div>
               
               <div className="overflow-x-auto border border-gray-100 rounded-lg">
                  <table className="w-full text-right text-sm">
                     <thead className="bg-gray-50 text-gray-500">
                        <tr>
                           <th className="px-6 py-4"></th>
                           <th className="px-6 py-4"></th>
                           <th className="px-6 py-4"> </th>
                           <th className="px-6 py-4"></th>
                           <th className="px-6 py-4"></th>
                        </tr>
                     </thead>
                     <tbody className="divide-y divide-gray-100">
                        {maintenanceLogs.map(log => (
                           <tr key={log.id} className="hover:bg-gray-50">
                              <td className="px-6 py-4 font-mono">{log.plateNumber}</td>
                              <td className="px-6 py-4 font-mono">{log.date}</td>
                              <td className="px-6 py-4"><span className="bg-gray-100 text-gray-600 px-2 py-1 rounded text-xs">{log.type}</span></td>
                              <td className="px-6 py-4">{log.description}</td>
                              <td className="px-6 py-4 font-bold text-red-600">{log.cost.toLocaleString()} .</td>
                           </tr>
                        ))}
                     </tbody>
                  </table>
               </div>
            </div>
         )}

         {/* --- GPS TRACKING TAB --- */}
         {activeTab === 'gps' && (
            <div className="flex flex-col lg:flex-row gap-6 h-[600px]">
               {/* Vehicles List */}
               <div className="w-full lg:w-1/3 bg-gray-50 rounded-xl border border-gray-200 p-4 overflow-y-auto">
                  <h3 className="font-bold text-gray-700 mb-4 flex items-center gap-2">
                     <Activity className="h-5 w-5 text-red-600" />
                       
                  </h3>
                  <div className="space-y-3">
                     {vehicles.map(v => (
                        <div key={v.id} className="bg-white p-3 rounded-lg shadow-sm border border-gray-100 hover:border-indigo-300 cursor-pointer transition-all">
                           <div className="flex justify-between items-start">
                              <div>
                                 <p className="font-bold text-sm text-gray-800">{v.model}</p>
                                 <p className="text-xs text-gray-500 font-mono">{v.plateNumber}</p>
                              </div>
                              <div className={`w-2 h-2 rounded-full ${v.gps && v.gps.speed > 0 ? 'bg-green-500 animate-pulse' : 'bg-gray-400'}`}></div>
                           </div>
                           {v.gps ? (
                              <div className="mt-3 pt-2 border-t border-gray-50 text-xs text-gray-600 space-y-1">
                                 <div className="flex justify-between">
                                    <span>:</span>
                                    <span className="font-mono font-bold">{v.gps.speed} km/h</span>
                                 </div>
                                 <div className="flex justify-between">
                                    <span>:</span>
                                    <span className="truncate max-w-[150px]">{v.gps.address}</span>
                                 </div>
                                 <div className="flex justify-between text-gray-400">
                                    <span>:</span>
                                    <span>{v.gps.lastUpdate}</span>
                                 </div>
                              </div>
                           ) : (
                              <p className="text-xs text-red-400 mt-2">   GPS</p>
                           )}
                        </div>
                     ))}
                  </div>
               </div>

               {/* Simulated Map Area */}
               <div className="flex-1 bg-slate-100 rounded-xl border border-gray-200 relative overflow-hidden group">
                  {/* Background Image simulating a map */}
                  <div className="absolute inset-0 bg-[url('https://upload.wikimedia.org/wikipedia/commons/e/ec/Cairo_downtown_map.png')] bg-cover bg-center opacity-60 grayscale group-hover:grayscale-0 transition-all duration-700"></div>
                  
                  {/* Map Markers */}
                  {vehicles.map((v, idx) => v.gps && (
                     <div 
                        key={v.id} 
                        className="absolute flex flex-col items-center"
                        style={{ 
                           top: `${40 + (idx * 15)}%`, 
                           left: `${30 + (idx * 20)}%`,
                           transform: 'translate(-50%, -50%)'
                        }}
                     >
                        <div className="relative">
                           <div className={`w-12 h-12 rounded-full flex items-center justify-center text-white shadow-lg ${v.gps.speed > 0 ? 'bg-indigo-600' : 'bg-gray-600'}`}>
                              <Car className="h-6 w-6" />
                           </div>
                           {v.gps.speed > 0 && <div className="absolute -inset-1 bg-indigo-500 rounded-full opacity-30 animate-ping"></div>}
                        </div>
                        <div className="mt-2 bg-white px-2 py-1 rounded shadow-md text-xs font-bold whitespace-nowrap">
                           {v.plateNumber}
                        </div>
                     </div>
                  ))}

                  <div className="absolute bottom-4 right-4 bg-white p-2 rounded shadow-md text-xs">
                     <p className="font-bold mb-1"> :</p>
                     <div className="flex items-center gap-2 mb-1"><span className="w-3 h-3 bg-indigo-600 rounded-full"></span> </div>
                     <div className="flex items-center gap-2"><span className="w-3 h-3 bg-gray-600 rounded-full"></span> </div>
                  </div>
               </div>
            </div>
         )}

      </div>
    </div>
  );
};

export default Transport;
